html, body {
  font-size: 14px;
  font-family: San Francisco ,-apple-system, BlinkMacSystemFont, ".SFSNText-Regular", Segoe UI, Ubuntu, Helvetica, sans-serif; }

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 15px; }

a {
  color: #2e92e6; }

.container {
  width: 998px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px; }

.u-text-center {
  text-align: center; }

.lead {
  font-size: 40px;
  color: #ddd;
  letter-spacing: -1px;
  line-height: 1.6;
  font-weight: 300; }

.main-nav {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px; }
  .main-nav .main-nav__section {
    display: flex; }
  .main-nav .main-nav__item {
    display: block;
    color: #0d0d0d;
    padding-left: 10px;
    padding-right: 10px;
    transition: 0.2s color; }
    .main-nav .main-nav__item, .main-nav .main-nav__item:hover {
      text-decoration: none; }
  .main-nav .main-nav__item:hover,
  .main-nav .main-nav__item--active {
    color: #2e92e6; }
  .main-nav .main-nav__item--non-link:hover {
    color: #0d0d0d; }
